import React, { Component } from 'react';
import styles from './Pagination.module.scss';
import { imagesOnPage } from "../../config/dict";

class Pagination extends Component {
  constructor(props) {
    super(props);
    this.handlePageClick = this.handlePageClick.bind(this);
    this.state = {
      currentPage: 1
    };
  }

  handlePageClick(pageNumber) {
    const leftBoundary = ((pageNumber -1) * this.props.imagesToShow);
    const rightBoundary = (pageNumber * this.props.imagesToShow);
    const newBoundaries = [leftBoundary, rightBoundary];
    this.props.onPageClick(newBoundaries);
    this.setState({ currentPage: pageNumber })
  }

  handleNumberOfImagesToShowClick(number) {
    this.props.onNumberOfImagesToShowClick(number);
    this.setState({ currentPage: 1})
  }

  render() {
    const nbOfPages = Math.ceil(this.props.totalImages / this.props.imagesToShow);
    const pagesArray = [...Array(nbOfPages).keys()].map(page => page + 1);
    console.log(pagesArray);
    const shouldPaginationBeShown = this.props.totalImages > this.props.imagesToShow;
    const shouldLeftArrowBeShown = this.state.currentPage > 1;
    const shouldRightArrowBeShown = this.state.currentPage < nbOfPages;
    const numbersOfImagesToShow = [9,18,36,72];

    return (
      shouldPaginationBeShown && (
      <div className={styles.wrapper}>
        {
          shouldLeftArrowBeShown &&
          <button
            className={styles['pages-button']}
            onClick={() => this.handlePageClick(1)}
          >
            <i className={"angle double left icon"}/>
          </button>
        }
        {
          shouldLeftArrowBeShown &&
          <button
            className={styles['pages-button']}
            onClick={() => this.handlePageClick(this.state.currentPage - 1)}
          >
            <i className={"angle left icon"}/>
          </button>
        }
        {
          pagesArray.map(pageNumber => (
            pageNumber > this.state.currentPage + 2  || pageNumber < this.state.currentPage - 2
              ? ''
              : <button
              key={pageNumber}
              onClick={() => this.handlePageClick(pageNumber)}
              className={pageNumber === this.state.currentPage ? styles.active : styles['pages-button']}
            >
              {pageNumber}
            </button>
          ))
        }
        {
          shouldRightArrowBeShown &&
          <button
            className={styles['pages-button']}
            onClick={() => this.handlePageClick(this.state.currentPage + 1)}
          >
            <i className={"angle right icon"}/>
          </button>
        }
        {
          shouldRightArrowBeShown &&
          <button
            className={styles['pages-button']}
            onClick={() => this.handlePageClick(nbOfPages)}
          >
            <i className={"angle double right icon"}/>

          </button>
        }
        <div className={styles['images-amount']}>
          <p className={styles['images-on-page']}>{imagesOnPage.imagesOnPage}</p>
          {
            numbersOfImagesToShow.map(imagesNumber => (
              <button
                onClick={() => this.handleNumberOfImagesToShowClick(imagesNumber)}
                className={styles['images-amount-button']}
              >
                {imagesNumber}
              </button>
            ))
          }
        </div>
      </div>
      )
    )
  }
}

export default Pagination;


