import React, { Component } from 'react';
import Heading from '../../common/components/Heading/Heading';
import styles from "../MyImages/MyImages.module.scss";
import { buttonsTexts, headingTexts } from "../../common/config/dict";
import Pagination from "../../common/components/Pagination/Pagination";

class MyImages extends Component {
  constructor(props) {
    super(props);
    this.handlePageClick = this.handlePageClick.bind(this);
    this.handleNumberOfImagesToShow = this.handleNumberOfImagesToShow.bind(this);
    this.state = {
      imagesToShow: 9,
      boundaries: [0, 9],
    };
  }

  handlePageClick(newBoundaries) {
    this.setState({ boundaries: newBoundaries })
  }

  handleNumberOfImagesToShow(imagesNumber) {
    this.setState({ imagesToShow: imagesNumber});
    this.setState({ boundaries: [0, imagesNumber]})
  }

  render() {

    const imagesToShow = this.props.listOfImages.slice(this.state.boundaries[0], this.state.boundaries[1]);

    return (
      <div>
        <section className={styles.wrapper}>
          <Heading text={headingTexts.myImages} />
          {
            imagesToShow.map(imageElem => (
              <div className={styles['dog-element']}>
                <img
                  src={imageElem.link}
                  alt={'dog-image'}
                  key={imageElem.link}
                  className={styles['dog-img']}
                />
                <button className={styles['delete-button']}
                        onClick={() => this.props.onDeleteClick(imageElem)}
                >
                  <span className={styles['delete-text']}>
                    {buttonsTexts.deleteButtonText}
                  </span>
                  <span className={styles['trash-icon']}>
                    <i className={"trash icon"}/>
                  </span>
                </button>
              </div>
            ))
          }
          <Pagination
            totalImages={this.props.listOfImages.length}
            imagesToShow={this.state.imagesToShow}
            onPageClick={this.handlePageClick}
            onNumberOfImagesToShowClick={this.handleNumberOfImagesToShow}
          />
        </section>
      </div>
    )
  }
}

export default MyImages


