import React from 'react';
import { waitTexts} from "../../config/dict";
import styles from './Wait.module.scss'

function Wait (props) {
  return (
    props.isWaiting
      ? (
        <div className={styles.wrapper}>
          <div className="ui text active loader">{waitTexts.loadingText}</div>
        </div>
      )
      : props.children
  );
}

export default Wait;


